* {
    margin: 0;
    padding: 0;
}

.app {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.board {
    width: calc(64px * 8);
    height: calc(64px * 8);
    display: flex;
    flex-wrap: wrap;
}

.cell {
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.cell img {
    height: 100%;
    width: 100%;
    position: relative;
    user-select: none;
}

.black {
    background-color: #739552;
}

.white {
    background-color: #ebecd0;
}

.selected {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgb(255, 255, 51) !important;
    opacity: 0.5;
}
.available {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 33%;
    height: 33%;
    background-color: rgba(0, 0, 0, 0.14) !important;
    border-radius: 50%;
    z-index: 1;
}
.captureAvailable {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 2;
    border: 5px solid rgba(0, 0, 0, 0.14);
    border-radius: 50%;

}
.lost{
    height: calc(50vh - 60px);
    padding: 30px;
    margin-left: 50px;
    background-color: lightgrey;
}
